body {
  cursor: none;
}
   
.cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: transparent; /* No fill by default */
  border: 2.5px solid var(--cursor-border-color, rgba(255, 255, 255, 0.7)); /* Dynamic border color */
  pointer-events: none;
  transform: translate3d(-50%, -50%, 0) scale(1); /* Center the cursor */
  transition: background-color 0.2s ease, border-color 0.2s ease;
  will-change: transform;
  z-index: 9999;
  box-shadow: var(--cursor-box-shadow, 0 0 8px rgba(255, 255, 255, 0.6)); /* Dynamic glow */
}

/* Style when hovering over clickable elements */
.cursor.hovering {
  background-color: rgba(255, 255, 255, 0.1); /* Slight fill */
  border-color: var(--cursor-hover-border-color, rgba(255, 105, 180, 0.7)); /* Dynamic border on hover */
  box-shadow: var(--cursor-hover-box-shadow, 0 0 12px rgba(255, 105, 180, 0.6)); /* Dynamic glow on hover */
}